// 
// _gridjs.scss
// 

.gridjs-container {
    padding: 0;
    display: block;
    @apply text-slate-500 dark:text-slate-400;
}

.gridjs-wrapper {
    box-shadow: none;
    border-radius: 0px;
    @apply border border-collapse custom-scroll border-gray-200 dark:border-gray-600;
    @apply rounded-t-md;
}

.gridjs-table {
    width: 100%;    
}

.gridjs-tbody,
td.gridjs-td {
    @apply bg-transparent;
}

th.gridjs-th,
td.gridjs-td {
    @apply border p-4 border-gray-200 dark:border-gray-600;
}

th {
    &.gridjs-th {
        @apply bg-transparent;
        border-top: 0;

    }

    &.gridjs-th-sort {

        &:focus,
        &:hover {
            @apply bg-transparent;
        }
    }
}

.gridjs-head {
    padding-top: 0;
}

.gridjs-footer {
    @apply shadow-none border border-t-0 bg-transparent border-gray-200 dark:border-gray-600;
}

.gridjs-summary {
    @apply text-gray-400;
    margin-top: 8px !important;
}

.gridjs-pagination {

    .gridjs-pages button {
        @apply text-gray-600 px-3 py-2 bg-transparent border border-gray-200 dark:border-gray-600;
        outline: none;
        user-select: none;

        &:first-child {
            @apply rounded-none rounded-s-md border border-gray-200 dark:border-gray-600;
        }

        &:last-child {
            @apply rounded-none rounded-e-md border border-gray-200 dark:border-gray-600;
        }

        &:disabled,
        &:hover:disabled,
        &[disabled] {
            @apply text-gray-600 bg-transparent;
        }

        &:hover {
            @apply text-primary bg-transparent;
        }

        &:focus {
            box-shadow: none;
        }

        &.gridjs-currentPage {
            @apply bg-primary text-white border-primary font-medium;
        }
    }

}

.gridjs-search {
    position: relative;
    float: left;

    &::before {
        content: "\f0d1";
        font-family: remixicon;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        inset-inline-start: 10px;
        font-size: 18px;
        @apply text-gray-400;
    }
}

input.gridjs-input {
    @apply form-input pe-3 ps-9;
}

th.gridjs-th {
    .gridjs-th-content {
        display: inline-block;
        vertical-align: middle;
        @apply font-semibold;
    }
}

button.gridjs-sort {
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 20px;
}

th.gridjs-th-sort .gridjs-th-content {
    width: calc(100% - 10px);
}

button {

    &.gridjs-sort-asc,
    &.gridjs-sort-desc {
        background-size: 7px;
    }
}


.gridjs-td {
    .gridjs-checkbox {
        vertical-align: top;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        appearance: none;
    }
}

.gridjs-border-none {

    td.gridjs-td,
    th.gridjs-th {
        border-inline-end-width: 0;
        border-inline-start-width: 0;
    }
}

.gridjs-loading-bar {
    @apply bg-transparent;
}