.simplebar-scrollbar {
  position: absolute;
  inset-inline-end: 2px;
  width: 4px;
  min-height: 10px;
}

.simplebar-scrollbar:before {
  content: "";
  position: absolute;
  border-radius: 7px;
  inset-inline-start: 0;
  inset-inline-end: 0;
  opacity: 0;
  transition: opacity 0.2s linear;
